<template>
  <v-card class="mx-auto mb-4" flat>
    <v-toolbar flat color="primary" dark>
      <v-toolbar-title>Gestion Utilisateur</v-toolbar-title>
    </v-toolbar>
    <v-data-table
      :headers="headers"
      :items="users"
      sort-by="email"
      class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>Liste des utilisateurs</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>
          <v-dialog v-model="dialog" max-width="500px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
                Nouvel Utilisateur
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-form ref="form" v-model="valid" lazy-validation>
                    <v-row>
                      <v-col cols="12" sm="10" md="10">
                        <v-text-field
                          v-model="editedItem.email"
                          :rules="emailRules"
                          label="email"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="10" md="10">
                        <v-text-field
                          v-model="editedItem.firstName"
                          :rules="firstNameRules"
                          label="Prnom"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="10" md="10">
                        <v-text-field
                          :rules="lastNameRules"
                          v-model="editedItem.lastName"
                          label="Nom"
                        ></v-text-field>
                      </v-col>
                      <v-col
                        cols="12"
                        sm="10"
                        md="10"
                        v-if="formTitle == 'Modification Utilisateur'"
                      >
                        <v-switch
                          label="Admin"
                          v-model="editedItem.admin"
                        ></v-switch>
                      </v-col>
                    </v-row>
                  </v-form>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">
                  Cancel
                </v-btn>
                <v-btn
                  color="blue darken-1"
                  :disabled="!valid"
                  text
                  @click="save"
                >
                  Save
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5"
                >Voulez vous vraiment supprimer cet utilisateur
                {{ editedItem.lastName + " " + editedItem.firstName }}
                ?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  >Cancel</v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                  >OK</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-icon class="mr-2" @click="sendEmailItem(item)" color="primary">
          mdi-send
        </v-icon>
        <v-icon class="mx-2" @click="editItem(item)"> mdi-pencil </v-icon>
        <v-icon class="ml-2" @click="deleteItem(item)" color="error">
          mdi-delete
        </v-icon>
      </template>
      <template v-slot:item.admin="{ item }">
        <v-chip color="primary" v-if="item.admin"> Admin </v-chip>
      </template>
      <template v-slot:no-data>
        <v-btn color="primary" @click="initialize"> Reset </v-btn>
      </template>
    </v-data-table>
  </v-card>
</template>

<script>
import userApi from "../../service/axios/user";
export default {
  data: () => ({
    dialog: false,
    dialogDelete: false,
    headers: [
      {
        text: "Adresse mail",
        align: "start",
        value: "email",
      },
      { text: "Nom", value: "firstName" },
      { text: "Prnom", value: "lastName" },
      { text: "Admin", value: "admin" },
      { text: "Actions", value: "actions", sortable: false },
    ],
    users: [],
    valid: true,
    editedIndex: -1,
    emailRules: [(v) => !!v || "Merci de saisir un Email"],
    firstNameRules: [(v) => !!v || "Merci de saisir un Prnom"],
    lastNameRules: [(v) => !!v || "Merci de saisir un Nom"],
    editedItem: {
      email: "",
      firstName: "",
      lastName: "",
      admin: false,
    },
    defaultItem: {
      email: "",
      firstName: "",
      lastName: "",
      admin: false,
    },
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? "Nouvel Utilisateur"
        : "Modification Utilisateur";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    async sendEmailItem(user) {
      var res = await userApi.resetPassword(user._id);
      console.log(res);
    },
    async initialize() {
      var res = await userApi.list();
      this.users = res.data;
      console.log(res);
    },

    editItem(item) {
      this.editedIndex = this.users.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.users.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    async deleteItemConfirm() {
      var res = await userApi.delete(this.editedItem._id);
      console.log(res);
      this.users.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      if (this.editedIndex == -1) {
        this.$refs.form.reset();
      }
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    async save() {
      if (this.$refs.form.validate()) {
        if (this.editedIndex > -1) {
          var res = await userApi.update(this.editedItem);
          this.editedItem = res.data;
          Object.assign(this.users[this.editedIndex], this.editedItem);
        } else {
          var res = await userApi.store(this.editedItem);
          console.log(res.data);
          this.users.push(res.data);
        }
        this.close();
      }
    },
  },
};
</script>

<style >
.v-data-table__wrapper {
  width: 100%;
}
</style>