<template>
  <v-card class="mx-auto mb-4" flat>
    <v-toolbar flat color="primary" dark>
      <v-toolbar-title>Gestion des Levels</v-toolbar-title>
    </v-toolbar>
    <v-data-table
      :headers="headers"
      :items="levels"
      sort-by="label"
      class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>Liste des levels</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>
          <v-dialog v-model="dialog" max-width="500px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
                Nouveau Level
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-form ref="form" v-model="valid" lazy-validation>
                    <v-row>
                      <v-col cols="12" sm="12" md="12">
                        <v-text-field
                          v-model="editedItem.label"
                          :rules="labelRules"
                          label="Dsignation"
                          required
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="12" md="12">
                        <v-select
                          :rules="definitionRules"
                          v-model="editedItem.definition"
                          :items="iconList"
                          label="Symbole"
                          required
                        >
                          <template slot="selection" slot-scope="data">
                            <v-icon :color="showColor">{{ data.item }}</v-icon>
                          </template>
                          <template slot="item" slot-scope="data">
                            <v-icon :color="showColor">
                              {{ data.item }}
                            </v-icon>
                          </template>
                        </v-select>
                      </v-col>
                      <v-col cols="12" sm="12" md="12">
                        <v-color-picker
                          v-model="color"
                          hide-inputs
                          required
                        ></v-color-picker>
                      </v-col>
                    </v-row>
                  </v-form>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">
                  Cancel
                </v-btn>
                <v-btn color="blue darken-1" text @click="save"> Save </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5"
                >Voulez vous vraiment supprimer l'icon :
                {{ editedItem.label }}?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  >Cancel</v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                  >OK</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-icon class="mx-2" @click="editItem(item)"> mdi-pencil </v-icon>
        <v-icon color="error" @click="deleteItem(item)"> mdi-delete </v-icon>
      </template>
      <template v-slot:item.definition="{ item }">
        <v-icon :color="item.color">{{ item.definition }}</v-icon>
      </template>
      <template v-slot:item.color="{ item }">
        <v-icon :color="item.color">mdi-circle</v-icon>
      </template>
      <template v-slot:no-data>
        <v-btn color="primary" @click="initialize"> Reset </v-btn>
      </template>
    </v-data-table>
  </v-card>
</template>

<script>
import levelApi from "../../service/axios/level";
export default {
  data: () => ({
    labelRules: [(v) => !!v || "Merci de saisir une dsignation"],
    definitionRules: [(v) => !!v || "Merci de choisir une icon"],
    dialog: false,
    type: "hex",
    hex: "#000000",
    iconList: [
      "mdi-alert",
      "mdi-alert-box",
      "mdi-circle",
      "mdi-check",
      "mdi-checkbox-marked",
      "mdi-chevron-double-right",
      "mdi-chevron-right",
      "mdi-chevron-right-box",
      "mdi-chevron-right-box-outline",
      "mdi-chevron-right-circle",
      "mdi-chevron-right-circle-outline",
      "mdi-chevron-triple-right",
    ],
    dialogDelete: false,
    headers: [
      {
        text: "Dsignation du niveau",
        align: "start",
        value: "label",
      },
      { text: "Couleur", value: "color" },
      { text: "Icone", value: "definition" },
      { text: "Actions", value: "actions", sortable: false },
    ],
    levels: [],
    valid: true,
    editedIndex: -1,
    editedItem: {
      label: "",
      color: "",
      definition: "",
    },
    defaultItem: {
      label: "",
      color: "",
      definition: "",
    },
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Nouveau Level" : "Modifier Level";
    },
    color: {
      get() {
        return this[this.type];
      },
      set(v) {
        this[this.type] = v;
      },
    },
    showColor() {
      if (typeof this.color === "string") return this.color;

      return JSON.stringify(
        Object.keys(this.color).reduce((color, key) => {
          color[key] = Number(this.color[key].toFixed(2));
          return color;
        }, {}),
        null,
        2
      );
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    async initialize() {
      var res = await levelApi.index();
      this.levels = res.data;
    },

    editItem(item) {
      this.editedIndex = this.levels.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.hex = this.editedItem.color;
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.levels.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    async deleteItemConfirm() {
      var res = await levelApi.delete(this.editedItem._id);
      console.log(res);
      this.levels.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      if (this.editedIndex == -1) {
        this.$refs.form.reset();
        this.hex = "#000000";
      }
      if (this.editedIndex != -1) {
        this.editedIndex = -1;
      }
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    async save() {
      if (this.$refs.form.validate()) {
        this.editedItem.color = this.showColor;
        if (this.editedIndex > -1) {
          var res = await levelApi.update(this.editedItem);
          Object.assign(this.levels[this.editedIndex], res.data);
        } else {
          var res = await levelApi.store(this.editedItem);
          this.levels.push(res.data);
        }
        this.close();
      }
    },
  },
};
</script>