import layout from "./layout/AppLayout";
import folderApi from "../../service/axios/folder";

export default {
    name: "archive",
    components: {
        layout,
    },
    created() {
        this.initialize();
    },
    methods: {
        getDateTime(date) {
            return new Date(date).toLocaleString("fr");
        },
        async initialize() {
            var res = await folderApi.foldersArchived();
            this.desserts = res.data;
            if (res.data) {
                this.load = false;
            }
        },
    },
    data() {
        return {
            search: "",
            load: true,
            expanded: [],
            isExpanded: false,
            headers: [{
                    text: "Nom du dossier",
                    align: "start",
                    value: "label",
                },
                { text: "Cr le", value: "createdAt" },
                { text: "Archiv le", value: "updatedAt" },
            ],
            headersTask: [{
                    text: "nom de la tache",
                    align: "start",
                    value: "title",
                },
                {
                    text: "cre le",
                    value: "createdAt",
                },
                {
                    text: "Termine le",
                    value: "updatedAt",
                },
                {
                    text: "Utilisateur",
                    value: "Users",
                },
            ],
            desserts: [],
        };
    },
};